<template>
    <div class="foodwrap">
        <header>
            <i class="el-icon-arrow-left" @click="goback"></i>
            <div class="ellipsis">
                <span>{{title}}</span>
            </div>
        </header>
        <section class="sort_container">
            <div class="top_nav">
                <div @click="chooseClass(0)" :class="{chooseBgc:showArr[0]}">
                    <span>{{showArr[0] ? '分类' : title}}</span>
                    <i class="el-icon-caret-bottom triangle"></i>
                </div>
                <transition name="el-zoom-in-top">
                <section v-if="showArr[0]">
                    <div class="drop_down_l">
                        <ul>
                            <li v-for="(item, index) in classification" :key="item.id" @click="changeClass(item, index)" :class="{bgc:order == index ? true : false}">
                                <section class="s1">
                                    <img :src="item.image_url ? 'https://fuss10.elemecdn.com/'+item.image_url : 'https://fuss10.elemecdn.com/default.png'" alt="">
                                    <span>{{item.name}}</span>
                                </section>
                                <section class="s2">
                                    <span>{{item.count}}</span>
                                    <i class="el-icon-arrow-right"></i>
                                </section>
                            </li>
                        </ul>
                    </div>
                    <div class="drop_down_r">
                        <ul>
                            <li v-for="(item, index) in categories" :key="item.id">
                                <span>{{item.name}}</span>
                                <span>{{item.count}}</span>
                            </li>
                        </ul>
                    </div>
                </section>
                </transition>
            </div>
            <div>
                <div @click="chooseClass(1)" :class="{chooseBgc:showArr[1]}">
                    <span>排序</span>
                    <i class="el-icon-caret-bottom triangle"></i>
                </div>
                <transition name="el-zoom-in-top">
                <section v-if="showArr[1]">
                    <ul class="list_container">
                        <li>
                            <span><i class="el-icon-sort"></i></span>
                            <p><span>智能排序</span></p>
                        </li>
                        <li>
                            <span><i class="el-icon-location-outline"></i></span>
                            <p><span>距离最近</span></p>
                        </li>
                        <li>
                            <span><i class="fa fa-free-code-camp"></i></span>
                            <p><span>销量最高</span></p>
                        </li>
                        <li>
                            <span><i class="fa fa-rmb"></i></span>
                            <p><span>起送价最低</span></p>
                        </li>
                        <li>
                            <span><i class="el-icon-time"></i></span>
                            <p><span>配送速度最快</span></p>
                        </li>
                        <li>
                            <span><i class="el-icon-star-off"></i></span>
                            <p><span>评分最高</span></p>
                        </li>
                    </ul>
                </section>
                </transition>
            </div>
            <div>
                <div @click="chooseClass(2)" :class="{chooseBgc:showArr[2]}">
                    <span>筛选</span>
                    <i class="el-icon-caret-bottom triangle"></i>
                </div>
                <transition name="el-zoom-in-top">
                <section class="filter_container" v-if="showArr[2]">
                    <div class="d1">
                        <p>配送方式</p>
                        <ul>
                            <li v-for="(item, index) in distribution_mode" :key="item.id">
                                <i class="fa fa-send"></i>
                                <span>{{item.text}}</span>
                            </li>
                        </ul>
                    </div>
                    <div class="d2">
                        <p>商家属性（可以多选）</p>
                        <ul>
                            <li>
                                <span class="filter_icon1">品</span>
                                <span>品牌商家</span>
                            </li>
                            <li>
                                <span class="filter_icon2">保</span>
                                <span>外卖保</span>
                            </li>
                            <li>
                                <span class="filter_icon3">准</span>
                                <span>准时达</span>
                            </li>
                            <li>
                                <span class="filter_icon4">新</span>
                                <span>新店</span>
                            </li>
                            <li>
                                <span class="filter_icon5">付</span>
                                <span>在线支付</span>
                            </li>
                            <li>
                                <span class="filter_icon6">票</span>
                                <span>开发票</span>
                            </li>
                        </ul>
                    </div>
                    <div class="d3">
                        <div>清空</div>
                        <div>确定<span>(0)</span></div>
                    </div>
                </section>
                </transition>
            </div>
        </section>
        <div class="back_cover" v-if="showArr[0] || showArr[1] || showArr[2]"></div>
        <section class="shoplist_container">
            <ul>
                <li v-for="(item, index) in shop" :key="item.id" class="shop_li" @click="toShop(item.id)">
                    <section>
                        <img :src="'https://elm.cangdu.org/img/'+item.image_path" alt="">
                    </section>
                    <div>
                        <div class="shop_title">
                            <h4><span>品牌</span>{{item.name}}</h4>
                            <ul class="supports">
                                <li>保</li>
                                <li>准</li>
                                <li>票</li>
                            </ul>
                        </div>
                        <h5 class="rating_num">
                            <div class="rating_left">
                                <el-rate :value="item.rating" disabled show-score  text-color="#ff6000" disabled-void-color="#d1d1d1">
                                </el-rate>
                                <span>月售{{item.recent_order_num}}单</span>
                            </div>
                            <div class="rating_right">
                                <span>蜂鸟专送</span>
                                <span>准时达</span>
                            </div>
                        </h5>
                        <h5 class="fee_distance">
                            <p>¥{{item.float_minimum_order_amount}}起送/配送费约¥{{item.float_delivery_fee}}</p>
                            <p><span class="distance">{{item.distance}}/</span><span class="hour">{{item.order_lead_time}}</span></p>
                        </h5>
                    </div>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
    import Vue from 'vue'
    export default {
        name: "Food",
        data(){
            return {
                title:"",
                classification:[],
                order:0,
                categories:[],
                isShow:100,
                showArr:[false,false,false],
                distribution_mode:[],
                shop:[]
            }
        },
        created(){
            this.title = this.$route.query.title;
            this.geohash = localStorage.getItem("geohash");
            let latitude = this.geohash.split(",")[0];
            let longitude = this.geohash.split(",")[1];
            this.axios.get("/shopping/restaurants",{
                params:{
                    latitude:latitude,
                    longitude:longitude,
                    restaurant_category_id:this.$route.query.restaurant_category_id
                }
            }).then(res=>{
                this.shop = res;
            }).catch(err=>{});
            this.axios.get("/shopping/v2/restaurant/category",{
                params:{
                    latitude:latitude,
                    longitude:longitude
                }
            }).then(res=>{
                this.classification = res;
                this.categories = res[0].sub_categories;
            }).catch(err=>{});
            this.axios.get("/shopping/v1/restaurants/delivery_modes",{
                params:{
                    latitude:latitude,
                    longitude:longitude
                }
            }).then(res=>{
                this.distribution_mode = res;
            }).catch(err=>{});
        },
        methods:{
            goback(){
                this.$router.go(-1);
            },
            changeClass(m, i){
                this.order = i;
                this.categories = m.sub_categories;
            },
            chooseClass(choose){
                for(let i = 0; i < 3; i++){
                    $(".triangle").eq(i).css("transform","rotateZ(0deg)");
                    if(i != choose){
                        Vue.set(this.showArr,i,false);
                    }
                }
                Vue.set(this.showArr,choose,!this.showArr[choose]);
                if(this.showArr[choose]){
                    $(".triangle").eq(choose).css("transform","rotateZ(180deg)");
                }
            },
            toShop(i){
                this.axios.get("/shopping/restaurant/"+i).then(res=>{
                    localStorage.setItem("current_shop",JSON.stringify(res));
                    this.$router.push({path:"/shop"});
                }).then(err=>{});
            }
        }
    }
</script>

